<template>
  <div id="app">
    <!-- Alert Tidak Punya Akses Halaman -->
    <v-col v-show="this.valid == false">
      <v-alert text type="error">
        Kamu tidak punya akses Ke halaman "Cabang"
      </v-alert>
    </v-col>

    <!-- Alert Data Tersimpan -->
    <v-alert style="position:fixed;z-index:2;right:20px;"
      dismissible
      color="success"
      border="left"
      elevation="4"
      colored-border
      v-model="Alert"
      transition="slide-x-transition"
    >
      <v-icon class="mr-1">check-circle</v-icon>
      Data Berhasil Disimpan
    </v-alert>

    <!-- Alert Data Sudah Terhapus -->
    <v-alert style="position:fixed;z-index:2;right:20px;"
      dismissible
      color="error"
      border="left"
      elevation="4"
      colored-border
      v-model="AlertDataTerhapus"
      transition="slide-x-transition"
    >
      Data Berhasil Dihapus
    </v-alert>

    <!-- Alert Data Berhasil Terubah -->
    <v-alert style="position:fixed;z-index:2;right:20px;"
      dismissible
      color="info"
      border="left"
      elevation="4"
      colored-border
      v-model="AlertBerhasilTerubah"
      transition="slide-x-transition"
    >
      Data Berhasil Diubah
    </v-alert>

    <div v-show="loading == true">
      <v-progress-linear
        :active="loading"
        :indeterminate="loading"
        absolute
        top
        height="6"
        color="red darken-4"
      ></v-progress-linear>
    </div>

    <!-- ---------------------------------------------------- -->

    <v-container fluid v-show="this.valid == true">
      <v-toolbar
        flat
        height="55"
        :color="$vuetify.theme.dark ? '' : 'grey lighten-2'"
        class="mb-3 rounded-lg"
        v-resize="onResize"
      >
        <v-toolbar-title>Data Cabang</v-toolbar-title>
        
        <v-spacer></v-spacer>

        <!-- Menu Export Data -->
        <div class="text-center mx-2">
          <v-menu
            v-model="MenuExport"
            :close-on-content-click="false"
            :nudge-width="1"
            rounded="lg"
            offset-y
          >
            <template v-slot:activator="{ on, attrs }">
              <!-- Btn Export Mode Biasa -->
              <v-btn
                @click="MenuExport = !MenuExport"
                class="text-capitalize rounded-lg"
                v-show="mobile == false"
                v-bind="attrs"
                v-on="on"
                outlined
                text
                :disabled="loading == true"
              >
                <v-icon left>mdi-table-arrow-down</v-icon>
                <span class="subtitle-1 mr-n1">Export Data</span>
                <v-icon right>{{ MenuExport ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
              </v-btn>
            </template>

            <v-card class="pa-2" v-show="MenuExport" :outlined="$vuetify.theme.dark ? true : false">   
              <!-- PDF -->
              <v-btn
                block
                depressed
                @click.native="PdfExport"
                class="text-capitalize subtitle-1 rounded-lg mb-2"
              >
                Pdf
              </v-btn>
            
              <!-- EXCEL -->
              <v-btn
                block
                depressed                       
                @click.native="excelExport"
                class="text-capitalize subtitle-1 rounded-lg"
              >
                Excel
              </v-btn>
            </v-card>
          </v-menu>

          <!-- Btn Export Dialog Mode Mobile -->
          <v-dialog
            v-model="DialogPilihExport"
            max-width="200"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                v-show="mobile == true"
                v-bind="attrs"
                v-on="on"
                outlined
                x-small text
                class="rounded-lg"
                height="40px" width="40px"
                :disabled="loading == true"
              >
                <v-icon>mdi-table-arrow-down</v-icon>
              </v-btn>
            </template>
            <v-card class="rounded-lg">
              <v-toolbar dense>
                <v-layout justify-center>
                  <v-toolbar-title>
                    Pilih Export File
                  </v-toolbar-title>
                </v-layout>
              </v-toolbar>
              <v-card class="pa-2">
                <v-btn text block outlined class="text-capitalize subtitle-1 mb-2" @click.native="PdfExport">
                  Pdf
                  <v-icon right>mdi-adobe-acrobat</v-icon>
                </v-btn>

                <v-btn text block outlined class="text-capitalize subtitle-1" @click.native="excelExport">
                  Excel
                  <v-icon right>mdi-microsoft-excel</v-icon>
                </v-btn>
              </v-card>
            </v-card>
          </v-dialog>
        </div>

        <!-- Dialog Tambah Cabang -->
        <v-dialog
          v-model="DialogTambahCabang"
          max-width="500px"
          scrollable
          persistent
        >
          <template v-slot:activator="{ on, attrs }">
            <!-- Btn Tambah Mode Desktop -->
            <v-btn
              class="text-capitalize mr-n2 rounded-lg"
              v-show="mobile == false && ValidCreate == true"
              v-bind="attrs"
              v-on="on"
              color="success"
              depressed
              :disabled="loading == true"
            >
              <v-icon left>mdi-plus-thick</v-icon>
              <span class="subtitle-1">Tambah</span>
            </v-btn>

            <!-- Btn Tambah Mode Desktop Kondisi Tidak bisa tambah user -->
            <v-btn
              class="text-capitalize mr-n2 rounded-lg"
              v-show="mobile == false && ValidCreate == false"
              v-bind="attrs"
              v-on="on"
              color="success"
              depressed
              :disabled="ValidCreate == false"
            >
              <v-icon left>mdi-plus-thick</v-icon>
              <span class="subtitle-1">Tambah</span>
            </v-btn>

            <!-- Btn Tambah Mode Mobile -->
            <v-btn 
              class="mr-n2 rounded-lg"
              v-show="mobile == true && ValidCreate == true"
              v-bind="attrs"
              v-on="on"
              x-small
              depressed
              color="success"
              height="40px" width="40px"
              :disabled="loading == true"
            >
              <v-icon>mdi-plus-thick</v-icon>
            </v-btn>
            
            <!-- Btn Tambah Mode Mobile Kondisi Tidak bisa tambah user -->
            <v-btn 
              class="mr-n2 rounded-lg"
              v-show="mobile == true && ValidCreate == false"
              x-small
              depressed
              color="success"
              height="40px" width="40px"
              :disabled="ValidCreate == false"
            >
              <v-icon>mdi-plus-thick</v-icon>
            </v-btn>
          </template>
          <v-card outlined class="rounded-lg">
            <v-toolbar flat>
              <v-toolbar-title>
                <span>{{ formTitleJabatan }}</span>
              </v-toolbar-title>
              <v-spacer></v-spacer>
              <v-btn
                icon
                @click="KeluarDialogCabang"
              >
                <v-icon>mdi-close</v-icon>
              </v-btn>
            </v-toolbar>

            <v-divider></v-divider>
            
            <v-card-text>
              <div class="mr-n3 ml-n3 mb-n2 mt-3">
                <v-sheet outlined class="rounded-lg pa-2">
                  <div class="pa-2">
                    <v-row no-gutters>
                      <v-col cols="12" sm="4" md="4" lg="4">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Kode Cabang</span>
                      </v-col>
                      <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                        <v-text-field
                          dense
                          color="dark"
                          v-model="editedItem.KodeCabang"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                        </v-text-field>
                      </v-col>
                    </v-row>

                    <v-row no-gutters>
                      <v-col cols="12" sm="4" md="4" lg="4">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Nama Cabang</span>
                      </v-col>
                      <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                        <v-text-field
                          dense
                          color="dark"
                          v-model="editedItem.NamaCabang"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                        </v-text-field>
                      </v-col>
                    </v-row>

                    <v-row no-gutters>
                      <v-col cols="12" sm="4" md="4" lg="4">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Alamat Cabang</span>
                      </v-col>
                      <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                        <v-text-field
                          dense
                          color="dark"
                          v-model="editedItem.AlamatCabang"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                        </v-text-field>
                      </v-col>
                    </v-row>

                    <v-row no-gutters>
                      <v-col cols="12" sm="4" md="4" lg="4">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Kode Perusahaan</span>
                      </v-col>
                      <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                        <v-autocomplete
                          dense
                          color="dark"
                          :items="DataPerusahaan"
                          :item-text="TextNamaPerusahaan"
                          item-value="KodePerusahaan"
                          v-model="editedItem.KodePerusahaan"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                        </v-autocomplete>
                        <!-- <v-text-field
                          dense
                          label="Kode Perusahaan"
                          color="dark"
                          v-model="editedItem.KodePerusahaan"   
                        ></v-text-field> -->
                      </v-col>
                    </v-row>

                    <v-row no-gutters>
                      <v-col cols="12" sm="4" md="4" lg="4">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Latitude</span>
                      </v-col>
                      <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                         <v-text-field
                          dense
                          color="dark"
                          v-model="editedItem.Latitude"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                        </v-text-field>
                      </v-col>
                    </v-row>

                    <v-row no-gutters>
                      <v-col cols="12" sm="4" md="4" lg="4">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Longitude</span>
                      </v-col>
                      <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                        <v-text-field
                          dense
                          color="dark"
                          v-model="editedItem.Longitude"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                        </v-text-field>
                      </v-col>
                    </v-row>

                    <v-row no-gutters>
                      <v-col cols="12" sm="4" md="4" lg="4">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Radius(M)</span>
                      </v-col>
                      <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                        <v-text-field
                          dense
                          color="dark"
                          v-model="editedItem.Radius"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                        </v-text-field>
                      </v-col>
                    </v-row>

                    <v-row no-gutters>
                      <v-col cols="12" sm="4" md="4" lg="4">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Timezone</span>
                      </v-col>
                      <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                        <v-text-field
                          dense
                          color="dark"
                          v-model="editedItem.TimeZone"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                        </v-text-field>  
                      </v-col>
                    </v-row>

                    <v-row no-gutters>
                      <v-col cols="12" sm="4" md="4" lg="4">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Version Barcode</span>
                      </v-col>
                      <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                        <v-text-field
                          dense
                          color="dark"
                          v-model="editedItem.VersionBarcode"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                        </v-text-field>
                      </v-col>
                    </v-row>
                  </div>
                </v-sheet>   
              </div>               
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
              <span class="d-inline-block text-truncate">
                <label>Status : <span>{{Status}}</span></label>
              </span>
              
              <v-spacer></v-spacer>

              <div class="mr-n2">
                <v-btn
                  dark
                  depressed
                  :color="$vuetify.theme.dark ? 'white black--text' : 'grey darken-3'"
                  class="text-capitalize rounded-lg"
                  @click="Simpan"
                >
                  <span>Simpan</span>
                </v-btn>
              </div>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>

      <v-card outlined class="pa-2 rounded-lg" :color="$vuetify.theme.dark ? '' : 'grey lighten-2'">
        <div>
          <ejs-grid
            ref='gridcabang'
            style="height: 100%;"
            :height="mobile ? '43vh' : '41vh'"
            gridLines='Both'
            id="cabangview"
            :dataSource="DataCabang"
            :allowPaging="true"
            :allowSorting='true'
            :allowResizing='true'
            :allowGrouping='true'
            :toolbar='toolbarOptions'
            :commandClick="commandClick"
            :pageSettings='pageSettings'
            :allowExcelExport='true'
            :allowPdfExport='true'
          >
            <e-columns>
              <e-column headerText="Aksi" textAlign='Center' width="120" :commands="commands" :lockColumn='true' :customAttributes="customAttributes">
                <div class="btn-group">
                  <v-btn type="button" class="btn btn-default" (click)="prediemRowEdit($event)">
                    <i class="fa fa-pencil"></i>
                  </v-btn>
                  <v-btn type="button" class="btn btn-default" (click)="prediemRowDelete($event)">
                    <i class="fa fa-trash"></i>
                  </v-btn>
                </div>
              </e-column>
              <e-column field='KodeCabang' headerText='Kode Cabang' textAlign='Left' width=150></e-column>
              <e-column field='NamaCabang' headerText='Nama Cabang' width=300></e-column>
              <e-column field='Alamat' headerText='Alamat' width=160></e-column>
              <e-column field='perusahaan.NamaPerusahaan' headerText='NamaPerusahaan' width=150></e-column>
              <e-column field='KodePerusahaan' headerText='Kode Perusahaan' :visible="false" width=160></e-column>
              <e-column field='Latitude' headerText='Latitude'  width=160></e-column>
              <e-column field='Longitude' headerText='Longitude'  width=160></e-column>
              <e-column field='Radius' headerText='Radius(M)'  width=120></e-column>
              <e-column field='TimeZone' headerText='TimeZone'  width=120></e-column>
              <e-column field='VersionBarcode' headerText='Versi Barcode'  width=120></e-column>
              <e-column field='DiBuatOleh' headerText='DiBuatOleh' textAlign='Left' width=150></e-column>
              <e-column field='DiBuatTgl' headerText='DiBuatTgl' textAlign='Left' width=150></e-column>
              <e-column field='DiUbahOleh' headerText='DiUbahOleh' textAlign='Left' width=150></e-column>
              <e-column field='DiUbahTgl' headerText='DiUbahTgl' textAlign='Left' width=150></e-column>
            </e-columns>
          </ejs-grid>
        </div>
      </v-card>
    </v-container>
  </div>
</template>
<script>
import Vue from "vue";
// import CryptoJS from 'crypto-js';
import { GridPlugin, Page, Sort, Group, Resize, Toolbar, Search, CommandColumn, ExcelExport, PdfExport } from "@syncfusion/ej2-vue-grids";
import api from "@/services/http";
Vue.use(GridPlugin);

export default {
  data() {
    return {
      keycrypt:"JfJbppnWXj3ASS5RSTROTQlbHGpfJIvjhKv7JiHMOgA=",
      mobile:null,
      valid:null,
      passbaru:null,
      Permission:[],
      DataPerusahaan:[],
      ValidCreate:false,
      KodeCabang:null,
      textAktif:true,
      umenu:[],
      verifpassbaru:null,
      loading:true,
      Kode:null,
      DialogPilihExport:false,
      windowSize: {x: 0, y: 0},
      MenuExport: false,
      commands: [],
      customAttributes : {class: 'customcss'},
      Alert:false,
      akun:[],
      usermenu:null,
      AlertDataTerhapus:false,
      AlertBerhasilTerubah:false,
      user:[],
      token:null,
      DialogTambahCabang: false,
      form: new FormData,
      editedIndex: -1,

      defaultItem: {
        KodeCabang: "",
        NamaCabang: "",
        AlamatCabang: "",
        KodePerusahaan: "",
        Latitude:"",
        Longitude:"",
        Radius:"",
        TimeZone:"",
        VersionBarcode:"",
      },
      editedItem: {
        KodeCabang: "",
        NamaCabang: "",
        AlamatCabang: "",
        KodePerusahaan: "",
        Latitude:"",
        Longitude:"",
        Radius:"",
        TimeZone:"",
        VersionBarcode:"",
      },
      toolbarOptions: ['Search'],
      DataCabang: [],
      pageSettings: {pageSize: 10, pageSizes :['5','10','15','20','50','All']},
    };
  },
  

  provide: {
    grid: [Page, Sort, Group, Resize, Toolbar, CommandColumn, Search,ExcelExport,PdfExport ]
  },

 async mounted(){
    this.token = localStorage.getItem('token')
    this.user = JSON.parse(localStorage.getItem('user'))
    if (this.Permission.some(a => a == 'R')) {
      this.valid = true
      this.getdata()
      this.getPerusahaan()
    }else{
      this.loading = false
      this.valid = false
    }
    if (this.Permission.some(a => a == 'C')) {
     this.ValidCreate = true
    }
    if (this.Permission.some(a => a == 'U')) {
     this.commands.push({buttonOption: { cssClass: "e-flat Edit", iconCss: "e-edit e-icons"} })
    }
    if (this.Permission.some(a => a == 'D')) {
     this.commands.push({ buttonOption: { cssClass:"e-flat Delete", iconCss: "e-delete e-icons"} })
    }
    
  },

  created(){
    //get action for this page
    let permisi = this.$route.params.Permission
    this.Permission = permisi == null ? [] : permisi.split(',')
    },

  computed: {
    formTitleJabatan() {
      return this.editedIndex === -1 ? 'Tambah Cabang Baru' : 'Ubah Data Cabang'
    },
    Status () {
      return this.editedIndex === -1 ? 'Baru' : 'Ubah'
    },
  },
  
  watch: {
    windowSize(){
      if (this.windowSize.x < 450) {
        this.titleClass = "d-none"
        this.mobile = true
      }else{
        this.titleClass = "mr-4"
        this.mobile = false
      }
    },

    DialogTambahCabang (val) {
      val || this.KeluarDialogCabang()
    },

    Alert(){
      this.AlertItem()
    },
    AlertDataTerhapus(){
      this.AlertItem()
    },
    AlertBerhasilTerubah(){
      this.AlertItem()
    },
  },

  methods: {
    print(){
      // Get HTML to print from element
        const prtHtml = document.getElementById('print').innerHTML;

        // Get all stylesheets HTML
        let stylesHtml = '';
        for (const node of [...document.querySelectorAll('link[rel="stylesheet"], style')]) {
          stylesHtml += node.outerHTML;
        }

        // Open the print window
        const WinPrint = window.open('', '', 'left=0,top=0,width=800,height=900,toolbar=0,scrollbars=0,status=0');

        WinPrint.document.write(`<!DOCTYPE html>
        <html>
          <head>
            ${stylesHtml}
          </head>
          <body>
            ${prtHtml}
          </body>
        </html>`);

        WinPrint.document.close();
        WinPrint.focus();
        WinPrint.print();
        WinPrint.close();
    },
    TextNamaPerusahaan(x){
      return x.KodePerusahaan + ' — ' + x.NamaPerusahaan
    },
    onResize(){
      this.windowSize = { x: window.innerWidth, y: window.innerHeight }
    },
    cariKode(Kode){
      if (Kode != null) {
          this.textAktif = false
        }else{
          this.textAktif = true
        }
      if (Kode == null || Kode == '' || Kode.length == 0) {
        this.editedIndex = -1
          this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      }else{
      this.Kode = Kode.toUpperCase()
      let a = this.DataCabang.filter( function(item){return (item.Kode == Kode.toUpperCase());} );
      if (a.length > 0) {
          this.editedIndex = 1;
          this.textAktif = false
          this.editedItem.Nama = a[0].Nama
        }
      }
    },
  async saya(){
       if (this.$menu == undefined) {
         await api.get('/menu?token='+this.token,{ headers: {"Authorization" : `Bearer ${this.token}`} }).then(
        res=>{
          if (res) {
            this.akun = res.data
            let route = this.$route.name
            let a = this.akun.filter( function(item){return (item.Route == route);} );
            if (a.length > 0) {
               this.valid = true
            }else{
              this.valid = false
              this.loading = false
            }
            //do noting
          }
        }).catch(err=>{
          // alert(err)
          if (err == "Error: Request failed with status code 401" && this.$route.path != "/login") {
            this.logout()
          }
        })
       }else{
        this.akun = this.$menu
        let route = this.$route.name
            let a = this.akun.filter( function(item){return (item.Route == route);} );
            if (a.length > 0) {
               this.valid = true
            }else{
              this.valid = false
              this.loading = false
            }
       }     
    },
    // encrypt(data){
    //   var CryptoJSAesJson = {
    //         stringify: function (cipherParams) {
    //             var j = {ct: cipherParams.ciphertext.toString(CryptoJS.enc.Base64)};
    //             if (cipherParams.iv) j.iv = cipherParams.iv.toString();
    //             if (cipherParams.salt) j.s = cipherParams.salt.toString();
    //             return JSON.stringify(j);
    //         },
    //         parse: function (jsonStr) {
    //             var j = JSON.parse(jsonStr);
    //             var cipherParams = CryptoJS.lib.CipherParams.create({ciphertext: CryptoJS.enc.Base64.parse(j.ct)});
    //             if (j.iv) cipherParams.iv = CryptoJS.enc.Hex.parse(j.iv)
    //             if (j.s) cipherParams.salt = CryptoJS.enc.Hex.parse(j.s)
    //             return cipherParams;
    //         }
    //     }
    //     return CryptoJS.AES.encrypt(JSON.stringify(data), this.keycrypt, {format: CryptoJSAesJson}).toString();
    // },

    Simpan(){
      if(this.formTitleJabatan === "Tambah Cabang Baru"){
        this.Toast('Data Disimpan')
        let data = {
            KodeCabang: this.editedItem.KodeCabang,
            NamaCabang: this.editedItem.NamaCabang,
            Alamat: this.editedItem.AlamatCabang,
            KodePerusahaan: this.editedItem.KodePerusahaan,
            Latitude:this.editedItem.Latitude,
            Longitude:this.editedItem.Longitude,
            Radius:this.editedItem.Radius,
            TimeZone:this.editedItem.TimeZone,
            VersionBarcode:this.editedItem.VersionBarcode
            }
          let encryptedata = this.$encrypt(data)
          // let encryptedata = CryptoJS.AES.encrypt(JSON.stringify(data), this.keycrypt).toString();
          console.log('encr',encryptedata)
          // var bytes  = CryptoJS.AES.decrypt(encryptedata, this.keycrypt);
          // var decryptedData = JSON.parse(bytes.toString(CryptoJS.enc.Utf8));
          // console.log('decr',decryptedData)
          api.post('/cabang?token='+this.token,{
            data:encryptedata
          })
            .then((res)=>{
              if (res) {
                // this.Alert = true
                this.getdata()
              } else {
                //
              }
            })
      }else if(this.formTitleJabatan === "Ubah Data Cabang"){
        this.Toast('Data Diubah')
        let data = {
            KodeCabang: this.editedItem.KodeCabang,
            NamaCabang: this.editedItem.NamaCabang,
            Alamat: this.editedItem.AlamatCabang,
            KodePerusahaan: this.editedItem.KodePerusahaan,
            Latitude:this.editedItem.Latitude,
            Longitude:this.editedItem.Longitude,
            Radius:this.editedItem.Radius,
            TimeZone:this.editedItem.TimeZone,
            VersionBarcode:this.editedItem.VersionBarcode
					}
          let encryptedata = this.$encrypt(data)
         api.put("/cabang/"+this.editedItem.KodeCabang+"/?token="+this.token,{
           data:encryptedata
         },
          { headers: {"Authorization" : `Bearer ${this.token}` } })
					.then((res) => {
						if (!res) {
							//do nothing
						}else{
              this.getdata()
              // this.AlertBerhasilTerubah = true
						}
						
					})
      }
      this.KeluarDialogCabang()    
    },

    commandClick: function(args) {
      if (args.target.classList.contains("custombutton")){
        // let data = JSON.stringify(args.rowData);
        // console.log(args.rowData);
      } else if (args.target.classList.contains("Delete")){
        var r = confirm("Yakin Hapus Data?");
        if (r == true) { 
          this.Toast('Data Dihapus')
          api
            .delete("/cabang/"+args.rowData.id+'?token='+this.token)
            .then((res) =>{
              // console.log(res);
              if (res) {
                this.getdata()
              this.AlertDataTerhapus = true
              }
              
            })
            .catch((err) => {
              console.log(err);
            });
        }
      } else if (args.target.classList.contains("Edit")){
        // console.log(args.rowData)
        this.KodeCabang = args.rowData.KodeCabang
        // let data = args
            this.editedIndex = 1;
            // console.log(data)
            this.editedItem.KodeCabang = args.rowData.KodeCabang
            this.editedItem.NamaCabang = args.rowData.NamaCabang
            this.editedItem.AlamatCabang = args.rowData.Alamat
            this.editedItem.KodePerusahaan = args.rowData.KodePerusahaan
            this.editedItem.Latitude = args.rowData.Latitude
            this.editedItem.Longitude = args.rowData.Longitude
            this.editedItem.Radius = args.rowData.Radius
            this.editedItem.TimeZone = args.rowData.TimeZone
            this.editedItem.VersionBarcode = args.rowData.VersionBarcode
            this.DialogTambahCabang = true
      }
    },

    PdfExport(){
      let data = document.getElementById('cabangview').ej2_instances[0];
      // console.log(data)
      data.pdfExportProperties = {
        pageOrientation: 'Landscape',
        fileName:"ReportJabatan.pdf",
      }
      data.columns[4].visible = false
      data.columns[5].visible = false
      data.columns[6].visible = false
      data.columns[7].visible = false
      this.$refs.gridcabang.pdfExport(data.pdfExportProperties);
    },
    excelExport(){
      this.$refs.gridcabang.excelExport();
    },

    KeluarDialogCabang () {
      this.DialogTambahCabang = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
        this.Kode = null
        this.textAktif = true
      })
    },

    // beforeExport: function(args) {
      
    // },

    getdata(){
      if (this.valid == true) {
        api.get('/cabang?token='+this.token,
        {
          transformResponse: api.defaults.transformResponse.concat((data) => {
            this.DataCabang = this.$decrypt(data)
            this.loading = false
          })
        }
        )
      //   .then(
			// 	res => {
      //     this.getPerusahaan()
			// 		console.log(res);
      //     this.DataCabang = res.data
      //     this.loading = false
			// 	},
			// 	err => {
			// 		console.log(err);
			// 	}
			// )
      }
      
    },

    getPerusahaan(){
        api.get('/perusahaan?token='+this.token,
        {
          transformResponse: api.defaults.transformResponse.concat((data) => {
            this.DataPerusahaan = this.$decrypt(data)
          })
        },
				err => {
					console.log(err);
				}
			)    
    },

    Toast(data){
      this.$swal.fire({
      toast: true,
      icon: 'success',
      title: data,
      animation: false,
      position: 'top-right',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true})
    },

    Reset() {
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
      })
    },
    logout(){
        api.post('/logout', this.token)
        .then(res=>{
          // console.log(res)
          if (res) {
            //do nothing
          }
        })
        localStorage.removeItem('token')
        localStorage.removeItem('user')
        location.reload(false)
      },

    AlertItem(){
      window.setInterval(() => {
        this.Alert = false;
        this.AlertDataTerhapus = false;
        this.AlertBerhasilTerubah = false;
      }, 3000)
    },
  },
}
</script>

